<!-- 忘记密码 -->
<template>
  <div class="cc-forget">
    <div class="cc-forget-wrapper">
      <!-- 上 -->
      <div class="logo-box">
        <img src="../../assets/images/logo.png" alt="">
      </div>
      <!-- 下 -->
      <div class="cc-forget-form" @keyup.enter="handlerLogin">
        <div class="title">
          <h1>忘记密码</h1>
        </div>
        <el-form ref="forgetForm" :model="forgetForm" :rules="rules">
          <el-form-item prop="accountNo">
            <el-input v-model.trim="forgetForm.accountNo" placeholder="请输入账号" clearable/>
          </el-form-item>
          <el-form-item prop="idCard">
            <el-input v-model.trim="forgetForm.idCard" placeholder="请输入身份证号码" clearable/>
          </el-form-item>
          <el-form-item prop="newPasswords">
            <el-input v-model.trim="forgetForm.newPasswords" type="password" placeholder="8~16位，至少包含数字/字母两种组合" clearable/>
          </el-form-item>
          <el-form-item prop="checkPasswords">
            <el-input v-model.trim="forgetForm.checkPasswords" type="password" placeholder="请确认密码" clearable/>
          </el-form-item>
          <el-form-item> 
            <div class="btn-box">
              <el-button type="primary" @click="handlerSave" class="btn save">确认</el-button>
              <el-button @click="handlerCancel" class="btn cancel">取消</el-button>
            </div>
          </el-form-item>
        </el-form>
      </div>
    </div>
  </div>
</template>

<script>
import formRule from "../../util/formRule";
export default {
  name: "cc-forget",
  data() {
    var validatePass = (rule, value, callback) => {
      if (value === '') {
        callback(new Error('请再次输入密码'));
      } else if (value !== this.forgetForm.newPasswords) {
        callback(new Error('两次输入密码不一致!'));
      } else {
        callback();
      }
    };
    return {
      forgetForm: {
        accountNo: '',
        idCard: '',
        newPasswords: '',
        checkPasswords: '',
      },
      rules: {
        accountNo: [
          { required: true, message: "用户名不能为空", trigger: "blur" },
          { validator: formRule.validateEAN, trigger: 'blur' }
        ],
        idCard:[
          { required: true, message: "身份证号不能为空", trigger: "blur" },
          { validator: formRule.validateIdcard, trigger: 'blur' }
        ],
        newPasswords: [
          { required: true, message: "密码不能为空", trigger: "blur" },
          { validator: formRule.validatePassword, trigger: 'blur' }
        ],
        checkPasswords: [
          { required:true,validator:validatePass, trigger: 'blur' }
        ]
      }
    };
  },
  created() {
  },
  methods: {
    //保存
    handlerSave() {
      this.$refs.forgetForm.validate(valid => {
        if (valid) {
          this.$http.post(process.env.VUE_APP_API+'/manager/user/forgetPasswords',{
            'accountNo':this.forgetForm.accountNo,
            'newPasswords':this.forgetForm.newPasswords,
            'idCard':this.forgetForm.idCard
          },false).then(res=>{
            if(res.code === 200){
              this.$message.success('修改成功')
              this.$router.push({path:'/login'})
            }else{
              this.$message.error(res.msg)
            }
          }).catch((error) => {
            this.$message.error(error)
          })
        }
      });
    },
    //取消
    handlerCancel(){
      this.$router.push({path:'/login'})
    }
  }
};
</script>

<style lang="less" scoped>
.cc-forget {
  background: url("../../assets/images/login-h.png") no-repeat center center;
  height: 100vh;
  background-size: cover;
  overflow: hidden;
  &-wrapper {
    height:90vh;
    width:80%;
    margin:30px auto;
    box-shadow: -7px 3px 13px 0px	rgba(255, 237, 207, 0.46);
    background-color:#fff;
    border-radius: 30px;
    background-size: contain;
    position:relative;
    padding:40px 50px;
    box-sizing:border-box;
    &::before{
      position:absolute;
      width:90%;
      height:70px;
      content:'';
      bottom:-70px;
      left:5%;
      background: url("../../assets/images/login-b.png") no-repeat center bottom 0px/cover;
    }
  }
  .logo-box{margin-bottom:3%;}
  &-form{
    width:300px;
    margin:0px auto;
    .title{
      margin-bottom:50px;
      h1{
        font-size:36px;
        color:#ff8a00;
        padding-bottom:20px;
        position: relative;
        &::after{
          position: absolute;
          content:'';
          left:0;
          bottom:0px;
          width:70px;
         	height: 2px;
	        background-image: linear-gradient(90deg, 	#ff8a00 0%,	#ffa904 100%);
        }
      }
    }
    .btn-box{
      display:flex;
      justify-content:space-between;
      .btn{
        height:42px;
        border-radius:6px;
        width:140px;
        font-size:16px;
        font-weight:bold;
        &.save{
          background-image: linear-gradient(-90deg, #fec532 0%, #fcac2e 100%);
          box-shadow: 0px 4px 8px 0px rgba(194, 142, 20, 0.3);
        }
        &.cancel{
          box-shadow: 0px 4px 8px 0px rgba(194, 142, 20, 0.3);
          border: solid 1px #ffc14f;
          color: #ffc14f;
        }
      }
    }
  }
}
</style>
<style lang="less">
.cc-forget-form{
  .el-input__inner{
    height:44px;
    line-height:44px;
    background-color: #fcf9f5;
    border-radius: 6px;
    border:none;
    .el-input__prefix,.el-input__suffix{
      height:44px;
      line-height:44px;
    }
  }
}

</style>

